/*----------------------------------*    Buttons
\*----------------------------------*/
.btn {
  font-weight: 600;
  text-transform: uppercase;
  font-size: 14px;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  padding: 18px 40px;
  border: none;
  line-height: 13px;
  -webkit-transform: scale(1);
  -moz-transform: scale(1);
  -ms-transform: scale(1);
  -o-transform: scale(1);
  transform: scale(1);
  -webkit-transition: 200ms;
  -moz-transition: 200ms;
  -o-transition: 200ms;
  transition: 200ms; }
  .btn:hover {
    -webkit-transform: scale(1.05);
    -moz-transform: scale(1.05);
    -ms-transform: scale(1.05);
    -o-transform: scale(1.05);
    transform: scale(1.05); }
  .btn__default {
    background-image: linear-gradient(to left, #53d44c, #4dbc46);
    box-shadow: 0 10px 40px 0 rgba(77, 188, 70, 0.25);
    color: white; }
  .btn--round {
    border-radius: 25px;
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px; }
  .btn--full {
    width: 100%; }
  .btn__delete {
    border: 1px solid #d43f3a;
    margin-top: 5px;
    border-radius: 4px;
    color: #fff;
    background-color: #d9534f;
    line-height: 1;
    -webkit-transition: 200ms;
    -moz-transition: 200ms;
    -o-transition: 200ms;
    transition: 200ms; }
    .btn__delete:hover {
      background-color: #de6764; }

