input[type=file], input[type=radio], input[type=checkbox] {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1; }

.radio {
  font-size: 17px; }
  .radio * {
    display: inline-block; }
  .radio span {
    width: 22px;
    height: 22px;
    border: 2.4px solid #d5d5d5;
    position: relative;
    -webkit-transition: 200ms;
    -moz-transition: 200ms;
    -o-transition: 200ms;
    transition: 200ms; }
    .radio span:after {
      content: "";
      position: absolute;
      height: 100%;
      width: 100%;
      background-color: #4dbc46;
      -webkit-transform: scale(0);
      -moz-transform: scale(0);
      -ms-transform: scale(0);
      -o-transform: scale(0);
      transform: scale(0);
      -webkit-transition: 200ms;
      -moz-transition: 200ms;
      -o-transition: 200ms;
      transition: 200ms; }
  .radio input:checked + span {
    border: 2.4px solid #4dbc46; }
    .radio input:checked + span:after {
      -webkit-transform: scale(0.6);
      -moz-transform: scale(0.6);
      -ms-transform: scale(0.6);
      -o-transform: scale(0.6);
      transform: scale(0.6); }

.round span {
  border-radius: 50%; }
  .round span:after {
    border-radius: 50%; }

.switch span {
  position: relative;
  background-color: #9e9e9e;
  height: 12px;
  width: 30px;
  border-radius: 6px;
  top: 4px;
  left: 4px;
  z-index: 0;
  -webkit-transition: 200ms;
  -moz-transition: 200ms;
  -o-transition: 200ms;
  transition: 200ms; }
  .switch span:before {
    content: "";
    position: absolute;
    top: -4px;
    left: -4px;
    height: 20px;
    border-radius: 50%;
    width: 20px;
    background-color: #f2f2f2;
    -ms-box-shadow: 0 0 3px 0 black;
    -o-box-shadow: 0 0 3px 0 black;
    box-shadow: 0 0 3px 0 black;
    z-index: 100;
    -webkit-transition: 200ms;
    -moz-transition: 200ms;
    -o-transition: 200ms;
    transition: 200ms; }

.switch input:checked + span {
  background-color: #95d791; }
  .switch input:checked + span:before {
    background-color: #4dbc46;
    left: calc(100% - 16px); }

